import { createFeatureSelector, createSelector } from '@ngrx/store';
import { IAssociationDesignerModel, IAssociationDesignerState } from './association-designer.types';
import { RX_ASSOCIATION_DESIGNER } from '../../association-designer.constant';

export const associationDesignerStateSelector = createFeatureSelector<IAssociationDesignerState>(
  RX_ASSOCIATION_DESIGNER.featureSelector
);

export const associationDesignerModelSelector = createSelector(
  associationDesignerStateSelector,
  (associationDesignerState: IAssociationDesignerState) => associationDesignerState.model
);

export const isDesignModeSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.isDesignMode
);

export const bundleIdSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.bundleId
);

export const definitionNameSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.definitionName
);

export const definitionModelSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.definitionModel
);

export const definitionModelFromDefinitionSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.definitionModelFromDefinition
);

export const isDirtySelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.isDirty
);

export const isForeignKeyCreatedSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.isForeignKeyCreated
);

export const isForeignKeyMissingSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.isForeignKeyMissing
);

export const savedDefinitionNameSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.savedDefinitionName
);

export const originalDefinitionSelector = createSelector(
  associationDesignerModelSelector,
  (associationDesignerModel: IAssociationDesignerModel) => associationDesignerModel.originalDefinition
);
